// Chat responses are cached per post
function getCurrentPostKey() {
  try {
    // Extract post identifier from URL
    const url = window.location.href;
    const match = url.match(/\/entries\/([^\/]+)\/index/);
    if (match) {
      return match[1]; // Returns the post name
    }
    return "new";
  } catch (error) {
    console.warn("Failed to get current post key:", error);
    return "new"; // Default to "new" if extraction fails
  }
}

// If the postKey has a chat, returns it. Otherwise returns the most recent
// chat from all posts, and caches all other post chats into history.
export function getCachedChatResponses(postKey = getCurrentPostKey()) {
  try {
    const allChats = getAllCachedChatResponses();
    const key = CACHE_KEYS.CHAT_RESPONSES + postKey;
    const cached = localStorage.getItem(key);
    if (allChats.length > 0) {
      const chat = cached ? JSON.parse(cached) : allChats[0];
      if (!cached) {
        // Set the chat into cache so this function remains idempotent
        setCachedChatResponses(chat.messages, chat.totalTokenCount, postKey);
      }
      // Cache all other post chats into history so they can be retrieved here
      allChats.forEach((c) => {
        if (c.timestamp !== chat.timestamp) {
          addChatToHistory(c, c.postKey);
        }
      });
      // Clear other posts' caches to avoid duplicate chats
      allChats.forEach((c) => {
        if (c.postKey !== postKey) {
          clearCachedChatResponses(c.postKey);
        }
      });
      return chat || null;
    }
    return cached ? JSON.parse(cached) : null;
  } catch (error) {
    console.warn("Failed to get cached chat responses:", error);
    return null;
  }
}

function getAllCachedChatResponses() {
  const allResponses = [];
  for (let i = 0; i < localStorage.length; i++) {
    const key = localStorage.key(i);
    if (key && key.startsWith(CACHE_KEYS.CHAT_RESPONSES)) {
      try {
        const cached = localStorage.getItem(key);
        const responsesForPost = cached ? JSON.parse(cached) : null;
        if (responsesForPost) {
          // Add a 'postKey' property to each response for display purposes
          const postKey = key.replace(CACHE_KEYS.CHAT_RESPONSES, "");
          responsesForPost.postKey = postKey;
          allResponses.push(responsesForPost);
        }
      } catch (error) {
        console.warn(`Failed to get chat responses for key ${key}:`, error);
      }
    }
  }
  // Sort by timestamp, newest first
  allResponses.sort((a, b) => b.timestamp - a.timestamp);
  return allResponses;
}

export function setCachedChatResponses(
  messages,
  totalTokenCount,
  postKey = getCurrentPostKey()
) {
  if (!messages || messages.length === 0) {
    console.warn("No messages provided to cache.");
    return;
  }
  try {
    const key = CACHE_KEYS.CHAT_RESPONSES + postKey;
    const cacheData = {
      messages: messages,
      totalTokenCount: totalTokenCount,
      timestamp: messages[messages.length - 1].timestamp || Date.now(),
    };
    localStorage.setItem(key, JSON.stringify(cacheData));
  } catch (error) {
    console.warn("Failed to cache chat responses:", error);
  }
}

export function clearCachedChatResponses(postKey = getCurrentPostKey()) {
  try {
    const key = CACHE_KEYS.CHAT_RESPONSES + postKey;
    localStorage.removeItem(key);
  } catch (error) {
    console.warn("Failed to clear cached chat responses:", error);
  }
}

// Clear all AI chat responses across all posts
export function clearAllChatResponseCaches() {
  try {
    const keysToRemove = [];
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (
        key &&
        (key.startsWith(CACHE_KEYS.CHAT_RESPONSES) ||
          key.startsWith(CACHE_KEYS.CHAT_HISTORY))
      ) {
        keysToRemove.push(key);
      }
    }
    keysToRemove.forEach((key) => localStorage.removeItem(key));
    console.log("Cleared all chat response caches");
  } catch (error) {
    console.warn("Failed to clear chat response caches:", error);
  }
}

export function getCachedChatHistory(postKey = getCurrentPostKey()) {
  try {
    const key = CACHE_KEYS.CHAT_HISTORY + postKey;
    const cached = localStorage.getItem(key);
    return cached ? JSON.parse(cached) : [];
  } catch (error) {
    console.warn("Failed to get cached chat history:", error);
    return [];
  }
}

export function getAllChatHistory() {
  const allHistory = [];
  for (let i = 0; i < localStorage.length; i++) {
    const key = localStorage.key(i);
    if (key && key.startsWith(CACHE_KEYS.CHAT_HISTORY)) {
      try {
        const cached = localStorage.getItem(key);
        const historyForPost = cached ? JSON.parse(cached) : [];
        // Add a 'postKey' property to each chat item for display purposes
        const postKey = key.replace(CACHE_KEYS.CHAT_HISTORY, "");
        historyForPost.forEach((chat) => (chat.postKey = postKey));
        allHistory.push(...historyForPost);
      } catch (error) {
        console.warn(`Failed to get chat history for key ${key}:`, error);
      }
    }
  }
  // Sort by timestamp, newest first
  allHistory.sort((a, b) => b.timestamp - a.timestamp);
  return allHistory;
}

export function setCachedChatHistory(history, postKey = getCurrentPostKey()) {
  try {
    const key = CACHE_KEYS.CHAT_HISTORY + postKey;
    localStorage.setItem(key, JSON.stringify(history));
  } catch (error) {
    console.warn("Failed to cache chat history:", error);
  }
}

export function addChatToHistory(chatData, postKey = getCurrentPostKey()) {
  if (!chatData || !chatData.messages || chatData.messages.length === 0) {
    return; // Don't save empty chats
  }
  const history = getCachedChatHistory(postKey);
  history.unshift(chatData); // Add to the beginning
  setCachedChatHistory(history, postKey);
}

export function deleteChatFromHistory(chatData) {
  if (!chatData || !chatData.messages || chatData.messages.length === 0) {
    return; // Don't delete empty chats
  }
  const history = getAllChatHistory();
  const index = history.findIndex(
    (h) => h.timestamp === chatData.timestamp && h.postKey === chatData.postKey
  );
  if (index !== -1) {
    history.splice(index, 1); // Remove the specific chat
    setCachedChatHistory(history, chatData.postKey);
  }
}

export function clearChatHistory(postKey = getCurrentPostKey()) {
  try {
    const key = CACHE_KEYS.CHAT_HISTORY + postKey;
    localStorage.removeItem(key);
  } catch (error) {
    console.warn("Failed to clear chat history:", error);
  }
}

export function getCachedSelectedModel() {
  const postKey = getCurrentPostKey();
  if (!postKey) return null;
  try {
    const key = CACHE_KEYS.SELECTED_MODEL + postKey;
    const cached = localStorage.getItem(key);
    return cached ? JSON.parse(cached) : null;
  } catch (error) {
    console.warn("Failed to get cached selected model:", error);
    return null;
  }
}

export function setCachedSelectedModel(model) {
  const postKey = getCurrentPostKey();
  if (!postKey) return;
  try {
    const key = CACHE_KEYS.SELECTED_MODEL + postKey;
    localStorage.setItem(key, JSON.stringify(model));
  } catch (error) {
    console.warn("Failed to cache selected model:", error);
  }
}

// Code samples cache utility functions
export function getCachedRepositories(username, includeForks) {
  try {
    const rType = includeForks ? "_all" : "_owner";
    const timestampId = TIME_ID_PREFIX.REPOSITORIES + username + rType;
    if (isCacheExpired(timestampId)) return null;
    const key = CACHE_KEYS.REPOSITORIES_LIST + username + rType;
    const cached = localStorage.getItem(key);
    return cached ? JSON.parse(cached) : null;
  } catch (error) {
    console.warn("Failed to get cached repositories:", error);
    return null;
  }
}

export function setCachedRepositories(username, repositories, includeForks) {
  try {
    const rType = includeForks ? "_all" : "_owner";
    const key = CACHE_KEYS.REPOSITORIES_LIST + username + rType;
    localStorage.setItem(key, JSON.stringify(repositories));
    const timestampId = TIME_ID_PREFIX.REPOSITORIES + username + rType;
    setCacheTime(timestampId);
  } catch (error) {
    console.warn("Failed to cache repositories:", error);
  }
}

export function getCachedRepositoryContent(user, repo, path) {
  try {
    // Repository content cache validity is tied to the parent repository list
    const timestampId = TIME_ID_PREFIX.REPOSITORIES + user + "_all"; // Assume forks might be included
    const timestampIdOwner = TIME_ID_PREFIX.REPOSITORIES + user + "_owner";
    if (isCacheExpired(timestampId) && isCacheExpired(timestampIdOwner)) {
      return null;
    }
    const key =
      CACHE_KEYS.REPOSITORY_CONTENT +
      `${user}_${repo}_` +
      btoa(path).replace(/[^a-zA-Z0-9]/g, "");
    const cached = localStorage.getItem(key);
    return cached ? JSON.parse(cached) : null;
  } catch (error) {
    console.warn("Failed to get cached repository content:", error);
    return null;
  }
}

export function setCachedRepositoryContent(user, repo, path, content) {
  try {
    const key =
      CACHE_KEYS.REPOSITORY_CONTENT +
      `${user}_${repo}_` +
      btoa(path).replace(/[^a-zA-Z0-9]/g, "");
    localStorage.setItem(key, JSON.stringify(content));
    // Timestamp is managed by the parent repository list cache
  } catch (error) {
    console.warn("Failed to cache repository content:", error);
  }
}

export function getCachedCodeSettings() {
  try {
    const timestampId = TIME_ID_PREFIX.CODE_SETTINGS;
    if (isCacheExpired(timestampId)) return null;
    const key = CACHE_KEYS.CODE_SETTINGS_CACHE;
    const cached = localStorage.getItem(key);
    return cached ? JSON.parse(cached) : null;
  } catch (error) {
    console.warn("Failed to get cached code samples:", error);
    return null;
  }
}

export function setCachedCodeSettings(codeSamples) {
  try {
    const key = CACHE_KEYS.CODE_SETTINGS_CACHE;
    localStorage.setItem(key, JSON.stringify(codeSamples));
    const timestampId = TIME_ID_PREFIX.CODE_SETTINGS;
    setCacheTime(timestampId);
  } catch (error) {
    console.warn("Failed to cache code samples:", error);
  }
}

export function clearCachedCodeAndSettings(user) {
  try {
    const keysToRemove = [];
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (
        key &&
        (key.startsWith(CACHE_KEYS.REPOSITORIES_LIST) ||
          key.startsWith(CACHE_KEYS.REPOSITORY_CONTENT) ||
          key.startsWith(CACHE_KEYS.CODE_SETTINGS_CACHE))
      ) {
        keysToRemove.push(key);
      }
    }
    keysToRemove.forEach((key) => localStorage.removeItem(key));
    clearCacheTime(TIME_ID_PREFIX.CODE_SETTINGS);
    clearCacheTime(TIME_ID_PREFIX.REPOSITORIES + user + "_all");
    clearCacheTime(TIME_ID_PREFIX.REPOSITORIES + user + "_owner");
    console.log("Code samples cache cleared");
  } catch (error) {
    console.warn("Failed to clear code samples cache:", error);
  }
}
